import fs from 'fs-extra';

/**
 *  secret 
 * -  name 
 * - 
 * - 
 */
const readSecret = async (
    /**  key */
    name: string
): Promise<string> =>
    (
        await fs.readFile(
            (process.env[name] || '').replace(
                /(\\|\/)*%(.+?)%(\\|\/)*/g,
                (match, p1 = '', p2, p3 = '') =>
                    `${p1}${process.env[p2] || ''}${p3}`
            ),
            'utf-8'
        )
    )
        .replace(/\n/g, '')
        .replace(/\r/g, '')
        .trim();

export default readSecret;
